div {
    -webkit-touch-callout: none;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}


.detail-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    /* 또는 center로 바꾸면 완전 세로 가운데 */
    padding-top: 6vh;
    padding-bottom: 3vh;
    background-color: #000;
    min-height: 100vh;
    color: #fff;
}

.team-title-image {
    max-width: 300px;
    height: auto;
    margin-bottom: 2rem;
    display: block;
}

/* 타이틀 스타일 */
.detail-title {
    font-size: 2rem;
    text-align: center;
    color: #e06363;
    font-family: 'Gowun Dodum', sans-serif;
    margin-bottom: 2rem;
}

/* 팀 캐릭터 목록 */
.team-scroll {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    width: 100%;
    max-width: 1200px;
    padding: 0 1rem;
}

/* 개별 카드 스타일 */
.person-card {
    width: 200px;
    height: 280px;
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid #e06363;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;
}

.person-card:hover {
    transform: scale(1.03);
}

/* 이미지가 박스에 꽉 차고, 중앙 맞춤 */
.person-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* 박스 채우기 */
    object-position: center;
    /* 얼굴 중앙 유지 */
    display: block;
}

.back-button-wrapper {
    display: flex;
    justify-content: center;
    margin: 2rem 0;
}

.back-button {
    background: linear-gradient(to top, #222, #444);
    border: 2px solid #666;
    color: #eee;
    font-family: 'IM Fell English SC', serif;
    padding: 12px 24px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1.2rem;
    text-shadow: 0 0 6px #ff0000;
    transition: all 0.3s ease;
}

.back-button:hover {
    background: linear-gradient(to top, #444, #666);
    transform: translateY(-4px);
    box-shadow: 0 0 12px #aa0000;
}